:root {
  --pale-violet: #d89eff;
  --moderate-violet: #9241c8;
  --desaturated-dark-violet: #6e5d7e;
  --grayish-blue: #c6cacd;
  --very-dark-desaturated-violet: #3e2753;
  --dark-grayish-violet: #a39daa;
  --light-magenta: #e942ff;
  --light-violet: #8838ff;
  --white: #fff;
  --light-grayish-violet: #f5f3f7;
  --very-dark-desaturated-violet: #3e2753;
  --very-light-magenta: #e570ff;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: Rubik, sans-serif;
  font-size: 1.6rem;
}

.phone-container {
  padding: 10rem 3rem;
  position: relative;
}

.background {
  width: 400px;
  height: 600px;
  background: linear-gradient(to bottom, var(--light-magenta), var(--light-violet));
  border-bottom-left-radius: 300px;
  border-bottom-right-radius: 300px;
  position: absolute;
  top: 0;
  left: -60%;
}

.phone {
  max-width: 270px;
  background-color: var(--white);
  border-radius: 3rem;
  margin: auto;
  padding: 1.1rem;
  position: relative;
  box-shadow: 0 0 100px #0003;
}

.phone:before {
  content: "";
  width: 130px;
  height: 30px;
  background-color: #fff;
  border-radius: 2rem;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.phone__screen {
  width: 100%;
  background-color: var(--light-grayish-violet);
  border-radius: 2rem;
}

.chat {
  padding: 1rem;
}

.contact-info {
  width: 100%;
  height: 80px;
  background-image: linear-gradient(to right, var(--light-violet), var(--light-magenta));
  border-radius: 2rem 2rem .8rem .8rem;
  justify-content: left;
  align-items: flex-end;
  padding: 1rem 1.5rem;
  display: flex;
}

.chevron {
  width: .8rem;
  height: 3px;
  margin: 0 .8rem 1.4rem 0;
}

.chevron-left, .chevron-right {
  background-color: var(--white);
}

.chevron-left {
  transform: rotate(120deg);
}

.chevron-right {
  transform: translateY(-12px)rotate(60deg);
}

.options {
  margin-bottom: 1rem;
  margin-left: auto;
}

.options__dot {
  width: 3px;
  height: 3px;
  background-color: var(--white);
  opacity: .7;
  border-radius: 50%;
  margin: 1px;
}

.media-obj {
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.avatar {
  width: 2.8rem;
  height: 2.8rem;
  border: 2px solid var(--white);
  border-radius: 50%;
  margin-right: -5px;
  overflow: hidden;
}

.avatar__image {
  width: 100%;
}

.name {
  color: var(--white);
  opacity: .8;
  margin-bottom: -3px;
  font-size: 1.2rem;
  font-weight: 500;
  display: block;
}

.status {
  opacity: .3;
  color: var(--white);
  font-size: 1rem;
}

.text {
  max-width: 60%;
  border-radius: 12px;
  margin: 1rem 0;
  padding: 1rem;
  font-size: .8rem;
}

.text--left {
  color: var(--moderate-violet);
  background-color: #9241c81a;
  border-bottom-left-radius: 5px;
}

.text--right {
  color: var(--desaturated-dark-violet);
  background-color: var(--white);
  width: fit-content;
  border-bottom-right-radius: 5px;
  margin-left: auto;
  box-shadow: 2px 5px 15px #96969652;
}

.images {
  justify-content: flex-end;
  gap: 1rem;
  margin: 1rem 0;
  display: flex;
}

.image {
  width: 40px;
  height: 40px;
}

.image img {
  width: 100%;
  border-radius: 10px;
}

.prices {
  background: linear-gradient(to right, var(--light-magenta), var(--light-violet));
  width: 80%;
  border-radius: 12px 12px 5px;
  align-items: center;
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
}

.radio-button {
  width: 15px;
  height: 15px;
  border: 2px solid var(--very-light-magenta);
  background-color: #0000;
  border-radius: 50%;
  margin-right: 1rem;
}

.duration {
  color: var(--white);
  font-size: 1rem;
}

.price {
  color: var(--white);
  margin-left: auto;
  font-weight: 700;
}

.form {
  background-color: var(--white);
  border-radius: 30px;
  align-items: center;
  padding: .5rem .5rem .5rem 2rem;
  display: flex;
}

.input {
  width: 90%;
  background: none;
  border: 0;
}

input::placeholder {
  color: var(--grayish-blue);
  font-size: 1.2rem;
}

.submit-button {
  background: var(--very-dark-desaturated-violet);
  width: 30px;
  height: 30px;
  color: var(--white);
  border: 0;
  border-radius: 50%;
  font-weight: 700;
}

.text-container {
  text-align: center;
  padding: 3rem;
}

.header {
  color: var(--very-dark-desaturated-violet);
  font-size: 3.5rem;
}

.body-text {
  color: var(--dark-grayish-violet);
  line-height: 1.8;
}

@media screen and (min-width: 1440px) {
  .container {
    align-items: center;
    display: flex;
  }

  .phone-container {
    width: 50%;
  }

  .phone {
    margin: 0 0 0 auto;
  }

  .text-container {
    width: 50%;
    text-align: left;
    padding: 0 10rem;
  }

  .header {
    font-size: 4rem;
  }

  .body-text {
    font-size: 1.8rem;
  }

  .background {
    width: 500px;
    height: 700px;
    border-bottom-left-radius: 40% 15%;
    border-bottom-right-radius: 60% 40%;
    left: -10px;
  }
}

.attribution {
  text-align: center;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

